import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index/index'
import activity from '@/pages/activityArea/activity'
import newsNotice from '@/pages/activityArea/newsNotice'
import newsText from '@/pages/activityArea/newsText'
import forgetPwd from '@/pages/forgetPwd/forgetPwd'
import retrievePwd from '@/pages/forgetPwd/retrievePwd'
import personalCenter from '@/pages/personalCenter/index'
import myInfo from '@/pages/personalCenter/myInfo'
import userInfo from '@/pages/personalCenter/userInfo'
import bindTencent from '@/pages/personalCenter/bindTencent'
import beanDetail from '@/pages/personalCenter/beanDetail'
import rankReward from '@/pages/personalCenter/rankReward'
import bankPwd from '@/pages/personalCenter/bankPwd'
import bindWeChat from '@/pages/personalCenter/bindWeChat'
import confirmLogin from '@/pages/personalCenter/confirmLogin'
import dailyRelief from '@/pages/personalCenter/dailyRelief'
import information from '@/pages/personalCenter/information'
import modifyPwd from '@/pages/personalCenter/modifyPwd'
import myBank from '@/pages/personalCenter/myBank'
import safePwd from '@/pages/personalCenter/safePwd'
import exchangeRecord from '@/pages/personalCenter/exchangeRecord'
import sendPacket from '@/pages/personalCenter/sendPacket'
import receiveRecord from '@/pages/personalCenter/receiveRecord'
import mySend from '@/pages/personalCenter/mySend'
import richText from '@/pages/richText/index'
import cooperativeBusiness from '@/pages/cooperativeBusiness/index'
import exeBean from '@/pages/personalCenter/exeBean'
import rankList from '@/pages/rankList/rankList'
import exchange from '@/pages/exchange/exchange'
import exchangeGoods from '@/pages/exchange/exchangeGoods'
import gameIndex from '@/pages/game/gameIndex'
import gameInfo from '@/pages/game/gameInfo'
import awardDetail from '@/pages/game/awardDetail'
import awardDetailBar from '@/pages/game/awardDetailBar'
import gameRecordInfo from '@/pages/game/gameRecordInfo'
import gameBet from '@/pages/game/gameBet'
import login from '@/pages/login/login'
import register from '@/pages/register/register'

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      redirect: {
        name: 'index'
      }
    },
    { // 
      path: '/index',
      name: 'index',
      meta: {
        title: '',
        noArrow: true, // 
        noBotBar: true //    
      },
      component: index
    },
    { // 
      path: '/activity',
      name: 'activity',
      meta: {
        title: '',
        noArrow: false,  
        noBotBar: false
      },
      component: activity
    }, { // 
      path: '/newsNotice',
      name: 'newsNotice',
      meta: {
        title: '',
        noArrow: false,  
        noBotBar: false
      },
      component: newsNotice
    },{ // 
      path: '/newsText',
      name: 'newsText',
      meta: {
        title: '',
        noArrow: false,  
        noBotBar: false
      },
      component: newsText
    }, { // 
      path: '/forgetPwd',
      name: 'forgetPwd',
      meta: {
        title: '',
        noArrow: false,  
        noBotBar: true,
        appBack: true
      },
      component: forgetPwd
    }, { // 
      path: '/retrievePwd',
      name: 'retrievePwd',
      meta: {
        title: '',
        noArrow: false,  
        noBotBar: true,
        appBack: false
      },
      component: retrievePwd
    }, { // 
      path: '/personalCenter',
      name: 'personalCenter',
      meta: {
        title: '',
        noArrow: true,
        noBotBar: true  
      },
      component: personalCenter
    },{ // 
      path: '/myInfo',
      name: 'myInfo',
      meta: {
        title: '',
        noArrow: false,  
        noBotBar: false
      },
      component: myInfo
    },{ // 
      path: '/userInfo',
      name: 'userInfo',
      meta: {
        title: '',
        noArrow: false,  
        noBotBar: false
      },
      component: userInfo
    },{ // QQ
      path: '/bindTencent',
      name: 'bindTencent',
      meta: {
        title: 'QQ',
        noArrow: false,  
        noBotBar: true
      },
      component: bindTencent
    },{ // 
      path: '/beanDetail',
      name: 'beanDetail',
      meta: {
        title: '',
        noArrow: false,  
        noBotBar: false
      },
      component: beanDetail
    },{ // 
      path: '/rankReward',
      name: 'rankReward',
      meta: {
        title: '',
        noArrow: false,  
        noBotBar: false
      },
      component: rankReward
    },{ // 
      path: '/exchangeRecord',
      name: 'exchangeRecord',
      meta: {
        title: '',
        noArrow: false,  
        noBotBar: false
      },
      component: exchangeRecord
    },{ // 
      path: '/sendPacket',
      name: 'sendPacket',
      meta: {
        title: '',
        noArrow: false,  
        noBotBar: false
      },
      component: sendPacket
    },{ // 
      path: '/receiveRecord',
      name: 'receiveRecord',
      meta: {
        title: '',
        noArrow: false,  
        noBotBar: false
      },
      component: receiveRecord
    },{ // 
      path: '/mySend',
      name: 'mySend',
      meta: {
        title: '',
        noArrow: false,  
        noBotBar: false
      },
      component: mySend
    },{ // 
      path: '/bankPwd',
      name: 'bankPwd',
      meta: {
        title: '',
        noArrow: false,  
        noBotBar: false
      },
      component: bankPwd
    }, { // 
      path: '/bindWeChat',
      name: 'bindWeChat',
      meta: {
        title: '',
        noArrow: false,  
        noBotBar: false
      },
      component: bindWeChat
    }, { // 
      path: '/confirmLogin',
      name: 'confirmLogin',
      meta: {
        title: '',
        noArrow: false,  
        noBotBar: false
      },
      component: confirmLogin
    }, { // 
      path: '/dailyRelief',
      name: 'dailyRelief',
      meta: {
        title: '',
        noArrow: false,  
        noBotBar: false
      },
      component: dailyRelief
    }, { // 
      path: '/information',
      name: 'information',
      meta: {
        title: '',
        noArrow: false,  
        noBotBar: false
      },
      component: information
    }, { // 
      path: '/modifyPwd',
      name: 'modifyPwd',
      meta: {
        title: '',
        noArrow: false,  
        noBotBar: false
      },
      component: modifyPwd
    }, { // 
      path: '/myBank',
      name: 'myBank',
      meta: {
        title: '',
        noArrow: false,  
        noBotBar: false
      },
      component: myBank
    }, { // 
      path: '/safePwd',
      name: 'safePwd',
      meta: {
        title: '',
        noArrow: false,  
        noBotBar: false
      },
      component: safePwd
    },{ // 
      path: '/exeBean',
      name: 'exeBean',
      meta: {
        title: '',
        noArrow: false,  
        noBotBar: false
      },
      component: exeBean
    },{ // 
      path: '/richText',
      name: 'richText',
      meta: {
        title: '',
        noArrow: false,  
        noBotBar: false
      },
      component: richText
    }, { // 
      path: '/cooperativeBusiness',
      name: 'cooperativeBusiness',
      meta: {
        title: '',
        noArrow: false,  
        noBotBar: false
      },
      component: cooperativeBusiness
    },{ // 
      path: '/rankList',
      name: 'rankList',
      meta: {
        title: '',
        noArrow: true,
        noBotBar: true
      },
      component: rankList
    },{ // 
      path: '/exchange',
      name: 'exchange',
      meta: {
        title: '',
        noArrow: true,  
        noBotBar: true
      },
      component: exchange
    },{ // 
      path: '/exchangeGoods',
      name: 'exchangeGoods',
      meta: {
        title: '',
        noArrow: false,  
        noBotBar: true
      },
      component: exchangeGoods
    },{
      // 
      path: '/gameIndex',
      name: 'gameIndex',
      component: gameIndex,
      meta: {
        title: '',
        noArrow: true,
        noBotBar: true
      }
    }, {
      path: '/gameInfo',
      name: 'gameInfo',
      component: gameInfo,
      meta: {
        title: '',
        noBotBar: false
      }
    }, {
      path: '/awardDetail',
      name: 'awardDetail',
      component: awardDetail,
      meta: {
        title: '',
        noBotBar: false
      }
    }, {
      path: '/awardDetailBar',
      name: 'awardDetailBar',
      component: awardDetailBar,
      meta: {
        title: '',
        noBotBar: false
      }
    }, {
      path: '/gameRecordInfo',
      name: 'gameRecordInfo',
      component: gameRecordInfo,
      meta: {
        title: '',
        noArrow: false,
        noBotBar: false
      }
    },{
      path: '/gameBet',
      name: 'gameBet',
      component: gameBet,
      meta: {
        title: '',
        noBotBar: false
      }
    }, {
      path: '/login',
      name: 'login',
      component: login,
      meta: {
        title: '',
        noArrow: false,
        noBotBar: false
      }
    }, {
      path: '/register',
      name: 'register',
      component: register,
      meta: {
        title: '',
        noArrow: false,
        noBotBar: false
      }
    }
  ]
})



