
import { getStore } from '../config/utils'  

const state = {
    userInfo: JSON.parse(getStore('userInfo')),
    haveLogin: getStore('haveLogin'),       // 
    newestItem: {},                         // 
    seconds: 0,                             //  
    timer: null,                            //  
    otherSeconds: 0,                        // 
    timerOther: null,                       // 
    newestState: 0,                         //   0   1 
    gameResultList: []                     //  
}

export default state


